/* body css */
.body {
    user-select:none;
    background:white;
    overflow:hidden;
}
/* //body css */

/* Header css */
.headerSearch {
    position:fixed;
    top:0;
    width:100%;
    height:100%;
    z-index:-1;
    background:rgba(0,0,0,0.3);
}

.headerSearchWrap {
    display:flex;
    justify-content:center;
    align-items:center;
    width:100vw;
    height:0;
    background:white;
    overflow:hidden;
    transition:height 0.4s ease;
}

.headerSearchSub {
    width:90vw;
    height:360px;
    margin:auto auto;
}

.headerSearchTop {
    text-align:right;
    height:50px;
    font-size:40px;
}

.headerSearchCloseIcon:hover {
    cursor:pointer;
}

.headerSearchBtm {
    margin-top:20px;
    position:relative;
    display:flex;
    justify-content:center;
    width:100%;
}

.headerSearchBtmWrap {
    display:flex;
    flex-direction:column;
    width:50vw;
    height:180px;
}

.headerSearchBtm p {
    font-weight:bold;
    font-size:30px;
}

.headerSearchBtm div {
    display:flex;
    justify-content:space-between;
    align-items:center;
    width:810px;
}

.headerSearchBtm input {
    width:650px;
    height:52px;
    font-size:20px;
    border:none;
    background:none;
    outline:none;
    border-bottom:2px solid black;
}

.headerSearchIcon {
    margin-top:140px;
    font-size:30px;
}

.headerSearchIcon:hover {
    cursor:pointer;
}

.header {
    position:fixed;
    top:0;
    width:100vw;
    background:white;
    border-bottom:1px solid rgba(0,0,0,0.1);
    box-sizing:border-box;
    z-index:10;
}

.headerNotif {
    height:70px;
    background:#3F7FFF;
    animation-duration:0.5s;
    animation-fill-mode:both;
    overflow:hidden;
}

@keyframes notifCloseAnim {
    100% {
        height:0;
        overflow:hidden;
    }
}

.headerNotifWrap {
    display:flex;
    justify-content:space-between;
    align-items:center;
    width:90vw;
    height:100%;
    margin:auto auto;
}

.headerNotifTxt {
    color:black;
    font-size:14px;
}

.headerNotifTxt span:not(:nth-child(2)) {
    font-weight:bold;
}

.headerNotifRight {
    position:relative;
    display:flex;
    justify-content:space-between;
    align-items:center;
    width:160px;
}

.headerNotifRight input {
    display:none;
}

.headerNotifRight label {
    display:flex;
    justify-content:center;
    align-items:center;
    width:13px;
    height:13px;
    border:1px solid gray;
    background:white;
}

.headerNotifRight label:hover {
    cursor:pointer;
}

.notifCheckIcon {
    font-size:11px;
    opacity:0.2;
}

.headerNotifRight label::after {
    content:"오늘 하루 보지 않기";
    position:absolute;
    font-size:12px;
    left:20px;
    color:#00000080;
}

.notifEXIcon {
    font-size:20px;
}

.notifEXIcon:hover {
    cursor:pointer;
}

.headerWrap {
    display:flex;
    flex-direction:column;
    width:90vw;
    height:190px;
    margin:auto auto;
}

.headerWrapTop {
    display:flex;
    justify-content:space-between;
    align-items:center;
    height:60px;
}

.hWTopLeft {
    display:flex;
    justify-content:space-between;
    width:125px;
    font-size:11px;
    color:#6E6E6E;
}

.hWTopLeft p:first-child::before {
    content:"관심상품 "
}

.hWTopLeft p:last-child::before {
    content:"좋아요 "
}

.hWTopLeft p:first-child::before,
.hWTopLeft p:first-child::before {
    font-weight:bold;
}

.hWTopLeft p:first-child::after,
.hWTopLeft p:last-child::after {
    content:"개";
    font-weight:bold;
}

.hWTopRight {
    display:flex;
    justify-content:space-between;
    width:320px;
    height:100%;
    font-size:11px;
}

.hWTopRight li {
    display:flex;
    align-items:center;
    font-weight:bold;
}

.hWTopRight li::before {
    content:"";
    width:1px;
    height:13px;
    margin-right:10px;
    background:#c9c9c9;
}

.hWTopRight li:first-child::before {
    width:0;
}

.hWTopRightLink {
    color:#6E6E6E;
}

.hWTopRightLink:hover {
    color:black;
}

.headerWrapMid {
    position:relative;
    display:flex;
    justify-content:center;
    align-items:center;
    height:70px;
}

.headerWrapMid ul {
    position:absolute;
    right:0;
    display:flex;
    width:130px;
    justify-content:space-between;
    font-size:30px;
}

.headerWrapMid ul li:first-child {
    cursor:pointer;
}

.hWMidRightLink {
    color:black;
}

.headerWrapBtm {
    margin-top:20px;
    display:flex;
    height:40px;
    justify-content:center;
}

.hWBWrap {
    display:flex;
    justify-content:space-between;
    width:410px;
    font-size:14px;
}

.hWBW_Sub {
    position:relative;
    display:flex;
    height:100%;
}

.hWBW_SubLink {
    color:#6E6E6E;
    background-image: linear-gradient(to bottom, transparent 92%, rgba(37,36,255));
    background-repeat: no-repeat;
    background-size:0% 100%;
    transition:0.15s ease-in-out;
    animation-duration:0.3s;
    animation-fill-mode:both;
}

@keyframes subLinkAnim_On {
    from {
        background-size:0 100%;
        background-position-x:left;
    }
    to {
        background-size:100% 100%;
        background-position-x:left;
    }
}

@keyframes subLinkAnim_Off {
    from {
        background-size:100% 100%;
        background-position-x:right;
    }
    to {
        background-size:0 100%;
        background-position-x:right;
    }
}

.hWBW_SubWrap {
    position:absolute;
    padding-top:10px;
    display:flex;
    flex-direction:column;
    top:100%;
    width:115px;
    text-align:center;
    border:1px solid rgb(189, 189, 189);
    box-sizing:border-box;
    background:white;
}

.hWBW_Sub:nth-child(1) .hWBW_SubWrap {
    left:-50%;
    height:170px;
}

.hWBW_Sub:nth-child(2) .hWBW_SubWrap {
    left:-35%;
    height:130px;
}

.hWBWSW_li {
    display:flex;
    height:100%;
}

.hWBWSW_Link {
    display:flex;
    justify-content:center;
    align-items:center;
    width:100%;
    color:#6E6E6E;
}

.hWBWSW_Link:hover {
    color:rgb(7, 7, 73);
}

.hWBWSW_Link::after {
    position:absolute;
    content:">";
    right:10px;
    font-size:10px;
    color: #6e6e6e;
}

.hWBW_Sub:nth-child(1) .hWBWSW_li:last-child .hWBWSW_Link::after,
.hWBW_Sub:nth-child(2) .hWBWSW_li:not(:last-child) .hWBWSW_Link::after {
    content:"";
}

.hWBWSW_Detail {
    padding-top:10px;
    position:absolute;
    display:flex;
    flex-direction:column;
    width:170px;
    height:100px;
    right:-140%;
    border:1px solid rgb(189, 189, 189);
    border-left:none;
    box-sizing:border-box;
    background:white;
    z-index:1;
}

.hWBW_Sub:nth-child(1) .hWBW_SubWrap:nth-child(2),
.hWBW_Sub:nth-child(2) .hWBW_SubWrap:nth-child(2) {
    display:none;
}

.hWBW_Sub:nth-child(1) .hWBWSW_li:nth-child(1) .hWBWSW_Detail {
    top:-1px;
    display:none;
}

.hWBW_Sub:nth-child(1) .hWBWSW_li:nth-child(2) .hWBWSW_Detail {
    top:40px;
    display:none;
}

.hWBW_Sub:nth-child(1) .hWBWSW_li:nth-child(3) .hWBWSW_Detail {
    top:75px;
    display:none;
}

.hWBW_Sub:nth-child(1) .hWBWSW_li:nth-child(4) .hWBWSW_Detail {
    top:105px;
    display:none;
}

.hWBW_Sub:nth-child(2) .hWBWSW_li:nth-child(4) .hWBWSW_Detail {
    padding-top:0;
    top:84px;
    right:-78%;
    width:100px;
    height:45px;
    display:none;
}

.hWBW_Sub:nth-child(2) .hWBWSW_li:nth-child(1) .hWBWSW_Detail,
.hWBW_Sub:nth-child(2) .hWBWSW_li:nth-child(2) .hWBWSW_Detail,
.hWBW_Sub:nth-child(2) .hWBWSW_li:nth-child(3) .hWBWSW_Detail {
    display:none;
    opacity:0;
    visibility:hidden;
}


.hWBWSW_DetailLi {
    width:100%;
    height:100%;
}

.hWBWSW_DetailLink {
    display:flex;
    justify-content:center;
    align-items:center;
    color:#6E6E6E;
    height:100%;
}

.hWBWSW_DetailLink:hover {
    color:rgb(7, 7, 73);
}
/* //Header css */

/* Banner css */
.banneri {
    margin-top:260px;
    display:flex;
    justify-content:center;
    align-items:flex-end;
    height:500px;
    background-size:cover;
    background-size:100% 100%;
    background-repeat:no-repeat;
    transition:0.4s;
    filter:opacity(100%);
}

.bannerBtnWrap {
    display:flex;
    justify-content:space-between;
    width:180px;
    height:30px;
}

.bannerBtn {
    width:35px;
    height:8px;
    background:rgba(0,0,0,0.3);
    border:none;
    z-index:1;
}

.bannerBtn:hover {
    cursor:pointer;
}

.bannerLink {
    position:absolute;
    width:100%;
    height:100%;
}
/* //Banner css */

/* Section css */
.section {
    display:flex;
    flex-direction:column;
}
    /* FirstSection css */
.firstSection {
    margin:0 auto;
    position:relative;
    display:flex;
    flex-direction:column;
    padding-top:100px;
    width:80%;
}

.firstSec {
    display:flex;
    width:100%;
    height:auto;
    flex-direction:column;
    align-items:center;
    margin-bottom:250px;
}

.firstSec h4 {
    font-size:25px;
    height:40px;
}

.firstSec p {
    font-weight:bold;
    font-size:15px;
    color:#8C8C8CCC;
}

.firstSec h4,
.firstSec p {
    text-align:center;
}

.firstSecMenuUL {
    margin-top:30px;
    margin-bottom:20px;
    display:flex;
    justify-content:space-between;
    width:400px;
    height:40px;
    font-size:20px;
    color:#8C8C8C;
}

.firstSecMenuUL li {
    background-image:linear-gradient(to bottom, transparent 92%, rgba(37,36,255));
    background-repeat:no-repeat;
    background-size:0 100%;
    transition:0.15s ease-in-out;
    animation-duration:0.3s;
    animation-fill-mode:both;
}

.firstSecMenuUL li:hover {
    cursor:pointer;
}

@keyframes firstSecMenuAnim_On {
    from {
        background-size:0 100%;
        background-position-x:left;
    }
    to {
        background-size:100% 100%;
        background-position-x:left;
    }
}

@keyframes firstSecMenuAnim_Off {
    from {
        background-size:100% 100%;
        background-position-x:right;
    }
    to {
        background-size:0 100%;
        background-position-x:right;
    }
}

.firstSecListUL {
    margin-top:30px;
    display:grid;
    width:100%;
    grid-template-columns:repeat(4, 1fr);
    grid-row-gap:110px;
    justify-items:center;
}

.firstSecList {
    display:flex;
    flex-direction:column;
    justify-content:end;
    width:280px;
    height:300px;
    background-size:100% 100%;
}

.firstSecListSub {
    display:flex;
    justify-content:right;
    align-items:center;
    height:90px;
    opacity:0;
    visibility:hidden;
    background:#FFFFFFCC;
    transition:0.3s;
    overflow:hidden;
}

.firstSecListSubWrap {
    display:flex;
    flex-direction:column;
    justify-content:space-between;
    width:95%;
    height:70px;
}

.firstSecListSubWrapBtm {
    display:flex;
    flex-direction:column;
    justify-content:space-between;
    height:38px;
}

.firstSecListSubWrapBtm span {
    color:rgb(63, 127, 255);
    font-weight:bold;
}

.firstSecListSubWrapTop,
.firstSecListSubWrapBtm {
    font-size:13px;
    color: rgba(140, 140, 140, 0.8);
}

.firstSecBtm {
    flex-grow:1;
    display:flex;
    align-items:flex-end;
    width:100%;
    height:500px;
    justify-content:space-between;
}

.firstSecBtm img {
    width:49%;
    height:370px;
}
    /* //FirstSection css */

    /* SecondSection css */
.secondSecImg {
    width:100%;
    height:500px;
    object-fit:cover;
}
    /* //SecondSection css */

    /* ThirdSection css */
.thirdSection {
    margin:0 auto;
    position:relative;
    padding-top:100px;
    display:flex;
    flex-direction:column;
    width:80%;
}

.thirdSec {
    display:flex;
    width:100%;
    flex-direction:column;
    align-items:center; 
}

.thirdSec button {
    margin-top:70px;
    width:260px;
    height:70px;
    border:1px solid rgba(158, 158, 158);
    background:white;
    box-sizing:border-box;
    font-size:15px;
    font-weight:bold;
    transition:border 0.2s;
}

.thirdSec button:hover {
    cursor:pointer;
    border-color:black;
}

.thirdSec h4 {
    font-size:25px;
    height:40px;
}

.thirdSec p {
    font-weight:bold;
    font-size:15px;
    color:#8C8C8CCC;
}

.thirdSec h4,
.thirdSec p {
    text-align:center;
}

.thirdSecListUL {
    margin-top:80px;
    display:grid;
    width:100%;
    grid-template-columns:repeat(4, 1fr);
    grid-row-gap:110px;
    justify-items:center;
}

.thirdSecList {
    display:flex;
    flex-direction:column;
    justify-content:end;
    width:280px;
    height:300px;
    background-size:100% 100%;
}

.thirdSecListSub {
    display:flex;
    justify-content:right;
    align-items:center;
    height:90px;
    opacity:0;
    visibility:hidden;
    background:#FFFFFFCC;
    transition:0.3s;
    overflow:hidden;
}

.thirdSecListSubWrap {
    display:flex;
    flex-direction:column;
    justify-content:space-between;
    width:95%;
    height:70px;
}

.thirdSecListSubWrapTop {
    font-size:13px;
}

.thirdSecListSubWrapBtm {
    display:flex;
    flex-direction:column;
    justify-content:space-between;
    height:38px;
}

.thirdSecListSubWrapBtm span:first-child {
    color:rgb(63, 127, 255);
    font-weight:bold;
    font-size:12px;
}

.thirdSecListSubWrapBtm span:last-child {
    width:30px;
    text-align:center;
    font-size:11px;
    color:white;
    background:black;
}

.thirdSecListSubWrapTop,
.thirdSecListSubWrapBtm {
    color: rgba(140, 140, 140, 0.8);
}
    /* //ThirdSection css */

    /* FourthSection css */
.fourSection {
    margin:0 auto;
    margin-top:250px;
    width:80%;
    height:680px;
}

.fourSec {
    display:flex;
    width:100%;
    flex-direction:column;
    align-items:center;
}

.fourSecTitle h4 {
    font-size:25px;
    height:40px;
}

.fourSecTitle p {
    font-weight:bold;
    font-size:15px;
    color:#8C8C8CCC;
}

.fourSecTitle h4,
.fourSecTitle p {
    text-align:center;
}

.fourSecUL {
    margin-top:50px;
    display:grid;
    grid-template-columns:repeat(2, 1fr);
    grid-column-gap:20px;
}

.fourSecUL li {
    display:flex;
    flex-direction:column;
    justify-content:space-between;
    height:420px;
}

.fourSecUL li:hover {
    cursor:pointer;
}

.fourSecULLink {
    color:black;
}

.fourSecULWrap {
    display:flex;
    flex-direction:row;
    justify-content:space-between;
    align-items:center;
}

.fourSecUL img {
    width:340px;
    height:340px;
}

.fourSecULTxt {
    display:flex;
    flex-direction:column;
    justify-content:space-between;
    height:65px;
}

.fourSecUL p {
    font-weight:bold;
    font-size:18px;
    color:#6E6E6E;
}

.fourSecUL span {
    font-weight:bold;
    font-size:15px;
    color:#969696;
}

.fourSecULBtn {
    display:flex;
    justify-content:center;
    align-items:center;
    width:30px;
    height:30px;
    font-size:20px;
    border:1px solid rgba(0, 0, 0, 0.5);
    border-radius:50%;
    box-sizing:border-box;
}
    /* //FourthSection css */
/* //Section css */

/* Footer css */
.footer {
    display:flex;
    flex-direction:column;
    justify-content:flex-end;
    width:100vw;
    height:500px;
    background:rgb(63, 180, 255);
}

.footerWrap {
    display:flex;
    flex-direction:column;
    justify-content:space-between;
    position:relative;
    margin:0 auto;
    width:90vw;
    height:400px;
} 

.footerWrap img {
    width:150px;
    height:40px;
}

.footerWrapTop {
    display:flex;
    flex-direction:column;
    justify-content:space-between;
    height:240px;
}

.fWT_Sub {
    display:flex;
    justify-content:space-between;
    height:130px;
}

.fWTS_Top {
    display:flex;
    flex-direction:column;
    justify-content:space-between;
    font-weight:bold;
}

.fWTST_linkUL {
    display:flex;
    justify-content:space-between;
    width:360px;
    font-size:15px;
}

.fWTST_link {
    color:#6E6E6E;
}

.fWTST_linkUL li:nth-child(3) .fWTST_link {
    color:#464646;
}

.fWTST_infoUL {
    display:flex;
    flex-direction:column;
    justify-content:space-between;
    height:80px;
}

.fWTST_infoUL_tit {
    font-size:14px;
}

.fWTST_infoUL ul {
    display:flex;
    flex-wrap:wrap;
    width:700px;
    height:45px;
    user-select:text;
    font-size:11px;
}

.fWTST_infoUL ul li {
    margin-right:10px;
}

.fWTST_infoUL ul li span:last-child {
    padding-left:5px;
    font-weight:normal;
    color:#646464;
}

.fWTS_Btm {
    display:flex;
    justify-content:space-between;
    width:430px;
    font-weight:bold;
}

.fWTSB_ctrUL {
    width:230px;
}

.fWTSB_payUL {
    width:180px;
}

.fWTSB_ctrUL_tit,
.fWTSB_payUL_tit {
    font-size:14px;
}

.fWTSB_ctrUL ul,
.fWTSB_payUL ul {
    font-size:12px;
    user-select:text;
}

.fWTSB_ctrUL ul li,
.fWTSB_payUL ul li {
    margin-top:5px;
}

.fWTSB_ctrUL ul li:first-child span:last-child {
    padding-left:10px;
}

.fWTSB_ctrUL ul li:first-child span:last-child,
.fWTSB_payUL ul li span:last-child {
    color:#646464;
}

.fWTSB_ctrUL ul li:nth-child(2),
.fWTSB_ctrUL ul li:last-child,
.fWTSB_payUL ul li {
    display:flex;
    flex-direction:column;
    justify-content:space-between;
    height:40px;
}

.fWTSB_payUL ul li span:last-child {
    display:flex;
    justify-content:space-between;
}

.fWTSB_payUL ul li span:last-child span:nth-child(1) {
    margin-right:10px;
}

.fWTSB_ctrUL ul li:first-child span:last-child,
.fWTSB_ctrUL ul li:nth-child(2) span:last-child,
.fWTSB_ctrUL ul li:last-child span:last-child,
.fWTSB_payUL ul li span:last-child span:nth-child(1) {
    font-weight:normal;
}


.footerWrapBtm {
    display:flex;
    justify-content:space-between;
    align-items:center;
    height:100px;
    border-top: 1px solid rgba(0,0,0,0.1);
}

.footerWrapBtm span {
    font-size:12px;
    color:#808080;
    user-select:text;
}

.footerWrapBtm:hover {
    cursor:text;
}

.fWB_Sub {
    display:flex;
    justify-content:space-between;
    align-items:center;
    width:330px;
}

.fWB_Sub span {
    font-size:15px;
    font-weight:bold;
    color:black;
}

.fWB_Sub ul {
    display:flex;
    justify-content:space-between;
    width:280px;
}

.fWB_Sub ul li a {
    font-size:25px;
    color:rgba(0,0,0,0.3);
}
/* //Footer css */

/* Dynamic css  */
@media (min-width:1920px) {
    .banneri {
        margin:0 auto;
        margin-top:260px;
        width:1500px;
    }

    .section {
        width:1500px;
        margin:0 auto;
    }
}

@media (max-width:1500px) {
    .firstSecListUL,
    .thirdSecListUL {
        width:700px;
        grid-template-columns:repeat(2, 1fr);
    }

    .firstSecBtm {
        display:flex;
        flex-direction:column;
        align-items:center;
        margin-top:100px;
        height:auto;
    }

    .firstSecBtm img {
        width:600px;
    }

    .firstSecBtm img:first-child {
        margin-bottom:50px;
    }
}

@media (max-width:1300px) {
    .headerSearchBtm div {
        width:550px;
    }
    
    .headerSearchBtm input {
        width:400px;
    }

    .footer {
        height:700px;
    }

    .footerWrap {
        width:60vw;
        height:600px;
    } 

    .fWT_Sub {
        flex-direction:column;
        align-items:center;
        text-align:center;
    }

    .fWTS_Top {
        align-items:center;
    }

    .fWTS_Top,
    .fWTST_linkUL {
        margin-bottom:50px;
    }

    .fWTST_infoUL ul {
        justify-content:center;
        width:500px;
    }

    .footerWrapBtm {
        flex-direction:column;
        justify-content:space-around;
    }
}

@media (max-width:650px) {
    .headerSearchBtm div {
        width:430px;
    }
    
    .headerSearchBtm input {
        width:300px;
    }

    .headerNotifWrap {
        flex-direction:column;
        justify-content:space-around;
    }

    .headerNotifTxt {
        font-size:11px;
    }

    .headerWrapMid ul {
        justify-content:space-evenly;
        width:100px;
        font-size:20px;
    }

    .firstSecListUL,
    .thirdSecListUL {
        width:700px;
        grid-template-columns:repeat(1, 1fr);
    }

    .firstSecBtm img {
        width:100%;
    }

    .secondSecImg {
        object-fit:fill;
    }

    .fourSection {
        height:auto;
        margin-bottom:100px;
    }

    .fourSecUL {
        grid-template-columns:repeat(1, 1fr);
        grid-row-gap:50px;
    }

    .fWTST_infoUL ul {
        width:400px;
    }
}
